<script setup>
import { ref, reactive } from 'vue';
import popupTitle from '@/components/popupTitle.vue';
import { useI18n } from 'vue-i18n';
const { t } = useI18n();
import { Toast } from '_vant@3.4.8@vant';
const emit = defineEmits(['closeFn', 'bindParent']);
defineProps({
  check: Boolean,
});
let address = sessionStorage.getItem('parent')
  ? ref(sessionStorage.getItem('parent'))
  : ref('');
const submit = () => {
  if (address.value == '') {
    Toast(t('Account.address'));
    return;
  }
  emit('bindParent', address.value);
};
</script>
<template>
  <div class="bind allFlex column">
    <popup-title
      :title="$t('Account.bindTitle')"
      @closeFn="$emit('closeFn')"
    ></popup-title>
    <div class="iptBg bind_ipt padding">
      <input
        type="text"
        class="Wcolor"
        v-model="address"
        :placeholder="$t('Account.address')"
      />
    </div>
    <van-button class="btn" type="primary" block @click="submit">{{
      check ? $t('Account.bindIng') : $t('Account.bind')
    }}</van-button>
  </div>
</template>

